


import { Component } from '@angular/core';
import { IonicPage, ModalController, NavController , ViewController} from 'ionic-angular';
import { Contacts, User } from '../../providers';
import { SocialSharing } from '@ionic-native/social-sharing';
import { CallNumber } from '@ionic-native/call-number';


@IonicPage()
@Component({
  selector: 'page-lead-pickead',
  templateUrl: 'lead-pickead.html'
})
export class LeadPickeadPage {

  currentItems: any;
  btnView = false;
  btnModal = false;
  btnCall = false;
  btnEmail = false;

  constructor(public viewCtrl : ViewController, private socialSharing: SocialSharing, private callNumber: CallNumber, public user: User, public navCtrl: NavController, public contacts: Contacts, public modalCtrl: ModalController) {

    this.initializeItems();
    //this.contacts.init(this.user.userInfo);

  }

  initializeItems() {
    this.currentItems = this.contacts.buyer;
  }


  getItems(ev: any) {

    this.initializeItems();
    const val = ev.target.value;

    if (val && val.trim() != '') {
      this.currentItems = this.currentItems.filter((item) => {
        return (item.fullname.toLowerCase().indexOf(val.toLowerCase()) > -1);
      })
    }
  }


  ionViewDidLoad() {

  }

  doRefresh(refresher) {


    let response = this.contacts.init(this.user.userInfo);

    Promise.all([response]).then(data => {
      refresher.complete();

    });
  }

  view(visitid, fullname) {


    this.viewCtrl.dismiss({ data: 5, fullname: fullname, folio: visitid });

  

  }

  addItem() {
    // if (this.btnModal)
    //   return;
    // this.btnModal = true;

    this.navCtrl.push('AddleadPage');

    // let addModal = this.modalCtrl.create('AddleadPage');

    // addModal.onDidDismiss(item => {
    //   this.btnModal = false;
    // });

    // addModal.present();
  }

  call(number, fullname) {

    if (this.btnCall)
      return;
    this.btnCall = true;

    this.callNumber.callNumber(number, true)
      .then(res => this.btnCall = false)
      .catch(err => this.btnCall = false);

  }

  mail(email, fullname) {

    if (this.btnEmail)
      return;
    this.btnEmail = true;

    this.socialSharing.shareViaEmail('', 'Asunto', [email]).then(() => {

      this.btnEmail = false;
    }).catch(() => {

      this.btnEmail = false;
    });

  }

}



